import React, {memo, useState} from "react";
import {PieChart} from "../../../../components/PieChart";
import {ClassNameElement} from "../../../../components/types";
import classNames from "classnames";

interface PieChartProductionProps extends ClassNameElement {
  setOuterActive?: (num: number) => void,
}

export interface PieChartDataProps {
  value: number,
  name: string,
  shift: string,
  selected?: boolean,
  itemStyle: { color: string },
}

const chartData: PieChartDataProps[][] = [
  [
    {value: 35.6, name: 'Внутренний рынок', shift: '-0.2', selected: true, itemStyle: {color: '#3a4798'}},
    {value: 64.4, name: 'Внешний рынок', shift: '+0.2', itemStyle: {color: '#71c5bc'}},
  ],
  [
    {value: 35.6, name: 'Внутренний рынок', shift: '-0.2', itemStyle: {color: '#3a4798'}},
    {value: 64.4, name: 'Внешний рынок', shift: '+0.2', selected: true, itemStyle: {color: '#71c5bc'}},
  ],
];

const PieChartSupply = memo(({className, setOuterActive}: PieChartProductionProps) => {
  const [active, setActive] = useState(0);

  return (
    <div className={classNames('pieChartProduction', className)}>
      <PieChart
        active={active}
        title='Структура поставок газа'
        desc='Кроме того, изменилась структура поставок газа по направлениям распределения ресурсов: доля экспорта (внешний рынок) уменьшилась на 0,4 п.п. — с 36,0% в 2019 г. до 35,6% в 2020 году. Доля внутреннего рынка, соответственно, возросла до 64,4% (против 64,0% годом ранее).'
        setActive={setActive}
        button='Подробнее в раздел'
        setOuterActive={setOuterActive}
        chartData={chartData}
        optionsStat={{
          type: 'pie',
          selectedMode: 'single',
          selectedOffset: 0,
          select: {
            itemStyle: {
              borderWidth: 8,
              borderRadius: 0,
              borderCap: 'square',
              borderColor: chartData[active][active].itemStyle.color,
              borderStyle: 'solid',
            },
          },
          radius: ['60%', '75%'],
          avoidLabelOverlap: false,
          label: {
            show: false,
          },
          labelLine: {
            show: false
          },
        }}
      />
    </div>
  )
});

export {PieChartSupply};