import classNames from 'classnames';
import React, {useEffect, useRef, useState} from 'react';
import {Link, NavLink, useLocation} from "react-router-dom";
import {IconLogo} from '../../icons/logo/logo';
import {Button} from '../Elements/Button';
import {Subheader} from './components';
import {PATHS, ROUTE} from './constants';
import {IconDownload} from "../../icons/download/download";
import {disableBodyScroll, enableBodyScroll} from "body-scroll-lock";

interface HeaderProps {
  sectionScroll: {
    [key: string]: number,
  },
}

const Header = ({sectionScroll}: HeaderProps) => {
  const [isOpened, setIsOpened] = useState<boolean>(false);

  const [isMobOpened, setIsMobOpened] = useState<boolean>(false);

  const navRef = useRef(null);

  const {pathname} = useLocation();

  useEffect(() => {
    setIsOpened(false);
  }, [pathname]);

  const toggleMobileMenu = () => {
    setIsOpened(!isOpened);

    if (navRef?.current) {
      isOpened ? disableBodyScroll(navRef.current) : enableBodyScroll(navRef.current);
    }
  };

  return (
    <>
      <header className='header'>
        <div className="header__content">
          <NavLink className='header__logo-wrapper' exact to={PATHS.HOME}>
            <IconLogo className='header__logo'/>
          </NavLink>
          <div className="header__nav-box">
            <button className={classNames("header__mobToggle", {'opened': isMobOpened})} onClick={() => {
              setIsMobOpened(!isMobOpened)
            }}><span className="header__mobToggle-line"/></button>
            <nav ref={navRef} className='header__nav'>
              {ROUTE.map(route => (
                <NavLink key={route.path} onClick={() => {
                  setIsMobOpened(false)
                }} className='header__nav-item' to={route.path}>
                  <p className='header__nav-value'>{route.name}</p>
                  {pathname === route.path && <button className={classNames({'opened': isOpened}, 'header__toggle')}
                                                      onClick={toggleMobileMenu}/>}
                </NavLink>
              ))}
            </nav>
            <Link to="/GasPages2020.pdf" target="_blank" download>
              <Button
                className='header__btnDownload'
                type='gray'
                onClick={() => {
                }}>
                <IconDownload/>
                <span>Скачать отчет</span>
              </Button>
            </Link>
          </div>
        </div>
      </header>
      <Subheader
        className={classNames({'visible': (pathname !== PATHS.HOME) && isOpened})}
        setIsOpened={setIsOpened}
        pathname={pathname}
        sectionScroll={sectionScroll}
      />
    </>
  )
}

export {Header};