import React, {memo, useState} from "react";
import {BarChart} from "../../../../components/BarChart";
import { BarDescField } from "../../../../components/BarChart/components/BarDescField";
import {data, dataNames} from "../../config/BarChartField";

const BarChartField = memo(() => {
  const [active, setActive] = useState(0);

  return (
    <div className='barChartField'>
      <BarChart
        className='barChartField__chart'
        option={{
          height: 250,
          animationDuration: 0,
          grid: {
            left: 150,
          },
          xAxis: {
            show: false,
          },
          yAxis: {
            type: 'category',
            data: dataNames,
            inverse: true,
            axisLine: {
              show: false,
            },
            axisLabel: {
              margin: 10,
              fontWeight: 700,
              fontSize: 15,
              color: '#000',
            },
            axisTick: {
              show: false,
            },
          },
          series: [
            {
              data: data,
              label: {
                show: true,
                position: 'right',
                fontWeight: 700,
                fontSize: 15,
                color: '#000',
                formatter: '{c} %',
              },
              type: 'bar'
            }
          ],
        }}
        setActive={setActive}
      />
      <div className="barChartField__mobile">
        <div className="barChartField__mobile-buttonBox">
          {dataNames.map((item, i) => (
            <button key={`${item}${i}`} onClick={() => setActive(i)} style={{backgroundColor: data[i].itemStyle.color}} className='barChartField__mobileButton'>{item}</button>
          ))}
        </div>
        <div className="barChartField__mobile-lineBox">
          <span className="barChartField__mobileLine" style={{width: `${data[active].value}%`,
            backgroundColor: data[active].itemStyle.color}}/>
          <span className="barChartField__mobileValue">{data[active].value}%</span>
        </div>
      </div>
      <BarDescField
        active={active}
      />
    </div>
  );
});

export {BarChartField};