import React, {memo, ReactElement, useEffect, useMemo, useRef, useState} from "react";
import ReactEcharts from "echarts-for-react";
import {EChartsOption} from "echarts-for-react/src/types";
import {ChartDesc} from "../ChartDesc";
import classNames from "classnames";
import {ChartDataProps, ClassNameElement} from "../types";
import {useWindowWidth} from "../../hooks/hooks";

interface BarVertChartProps extends ClassNameElement {
  chartData: ChartDataProps,
  interval?: number,
  withButton?: boolean,
  modalContent?: ReactElement,
  measurement?: string,
}

const BarVertChart = memo(({className, chartData, interval = 160, withButton = false, modalContent, measurement}: BarVertChartProps) => {
  const [active, setActive] = useState(chartData.length - 2);
  const [newData, setNewData] = useState<ChartDataProps>([]);

  useEffect(() => {
    setNewData(chartData.map((item, i) => {
      if (i === chartData.length - 1) {
        item.itemStyle = {
          color: '#1B3A98',
        }
      }
      return item;
    }));
  }, [active, chartData]);

  const chartConfig = {
    title: {
      subtext: measurement || 'млрд куб. м',
    },
    height: 200,
    grid: {
      left: 40,
    },
    xAxis: {
      type: 'category',
      data: ['2011', '2012', '2013', '2014', '2015', '2016', '2017', '2018', '2019', '2020'],
      axisTick: {
        show: false,
      },
    },
    yAxis: {
      axisLine: {
        show: false,
      },
      axisLabel: {
        color: '#000',
      },
      minInterval: interval,
      axisTick: {
        show: false,
      },
    },
    animationDuration: 0,
    series: [
      {
        data: newData,
        type: 'bar',
        itemStyle: {
          color: '#71c5bc',
        },
        barMaxWidth: 30,
        label: {
          show: true,
          position: 'top',
          fontWeight: 700,
          fontSize: 15,
          color: '#000',
          formatter: (config: any) => {
            return config.dataIndex === active || config.dataIndex === newData.length - 1 ? config.data.value : '';
          },
        },
      },
    ],
  };

  const chartMobileConfig = {
    width: 320,
    height: 125,
    grid: {
      left: 40,
      top: 30,
    },
    xAxis: {
      type: 'category',
      data: ['2011', '2012', '2013', '2014', '2015', '2016', '2017', '2018', '2019', '2020'],
      axisLabel: {
        fontSize: 8,
      },
      axisTick: {
        show: false,
      },
    },
    yAxis: {
      axisLine: {
        show: false,
      },
      axisLabel: {
        color: '#000',
        fontSize: 8,
      },
      minInterval: interval,
      axisTick: {
        show: false,
      },
    },
    animationDuration: 0,
    series: [
      {
        data: newData,
        type: 'bar',
        itemStyle: {
          color: '#71c5bc',
        },
        barMaxWidth: 15,
        label: {
          show: true,
          position: 'top',
          fontWeight: 700,
          fontSize: 12,
          color: '#000',
          formatter: (config: any) => {
            return config.dataIndex === active || config.dataIndex === newData.length - 1 ? config.data.value : '';
          },
        },
      },
    ],
  };

  return (
    <section className={classNames("barVertChart", className)}>
      <div className="barVertChart__chart-box">
        <ReactEcharts
          className='barVertChart__chart barVertChart__chart--aux'
          option={useWindowWidth() < 768 ? chartMobileConfig : chartConfig}
          onEvents={{
            mouseover: (e: EChartsOption) => {
              if (e?.dataIndex === chartData.length - 1) {
                setActive(chartData.length - 2);
              } else {
                setActive(e?.dataIndex);
              }
            },
          }}
        />
      </div>
      <ChartDesc
        className='barVertChart__desc'
        chartData={chartData}
        active={active}
        measurement={measurement || 'млрд куб. м'}
        withButton={withButton}
        modalContent={modalContent}
      />
    </section>
  );
});

export {BarVertChart};