import React, {memo} from "react";
import {prevChartDataMultiItem} from "../LineChart";

interface ChartDescPrevMultiProps {
  chartData: prevChartDataMultiItem[],
  active: number,
  title: string,
  measurement: string,
  desc?: string,
  activePrev: number,
}

const ChartDescPrevMulti = memo(({chartData, active, title, measurement, desc, activePrev}: ChartDescPrevMultiProps) => {
  const showValueChange = () => {
    return Math.round((chartData[active].value - chartData[active].prev[activePrev]) * 10) / 10;
  };

  const showShiftChange = () => Math.round(showValueChange() / (chartData[active].value / 100) * 10) / 10;

  return (
    <div className="chartDescPrevMulti">
      <p className="chartDescPrevMulti__title">{title}</p>
      <b className="chartDescPrevMulti__shift">{showShiftChange()}%</b>
      <b className="chartDescPrevMulti__value">{showValueChange()}</b>
      <span className="chartDescPrevMulti__measurement">{measurement}</span>
      <p className="chartDescPrevMulti__desc">{desc}</p>
    </div>
  );
});

export {ChartDescPrevMulti};