import React, {useEffect, useMemo, useState} from 'react';
import {PATHS} from "../constants";
import {CONFIG, GAS_NAVIGATION, HEADER_WIDTH} from './constants/config';
import classNames from "classnames";
import {ClassNameElement} from "../../types";

interface SubheaderProps extends ClassNameElement {
  pathname: string,
  setIsOpened: (bool: boolean) => void,
  sectionScroll: {
    [key: string]: number,
  },
}

const Subheader = ({pathname, setIsOpened, className, sectionScroll}: SubheaderProps) => {


  const subheaderValues = (useMemo(() => () => {
    switch (pathname) {
      case PATHS.GAS:
        return CONFIG.GAS;
      default:
        break;
    }
  }, [pathname]))();


  return (
    <div className={classNames("subheader", className)}>
      <div className="subheader__wrapper">
        <p className="subheader__desc">Показатели:</p>
        <ul className='subheader__content'>
          {subheaderValues?.length && subheaderValues.map((value, i, arr) => {
            return (
              <li key={value.nameShort}>
                <a
                  className={classNames('subheader__value', {'active': arr[i + 1]?.path ? sectionScroll[value.path] <= HEADER_WIDTH && sectionScroll[arr[i + 1]?.path] > HEADER_WIDTH : sectionScroll[value.path] <= HEADER_WIDTH})}
                  href={`#${value.path}`}>{value.nameShort}</a>
              </li>
            )
          })}
        </ul>
        <button className="subheader__toggle" onClick={() => {setIsOpened(false);}}/>
      </div>
    </div>
  )
};

export {Subheader};
